"use client";

import { useState, useEffect } from "react";
import Sidebar, { SidebarView } from "@/components/Sidebar";
import BudgetReportPage from "@/app/budget-report/page";
import UserReportPage from "@/app/user-report/page";
import UserMonthlyDashboard from "@/app/user-monthly-dashboard/page";
import BudgetManagement from "@/components/BudgetManagement";
import Login from "@/app/login/page"; // ‚Üê „Åì„Çå„Çí‰Ωø„ÅÜÔºà„Åæ„Åü„ÅØ components/Login.tsxÔºâ

export default function HomePage() {
  const [view, setView] = useState<SidebarView>("calendar");
  const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null);

  useEffect(() => {
    const auth = localStorage.getItem("isAuthenticated");
    setIsAuthenticated(auth === "true"); // ‚Üê "true" „Å®„ÅÑ„ÅÜÊñáÂ≠óÂàó„Åß‰øùÂ≠ò„Åï„Çå„Å¶„ÇãÂâçÊèê
  }, []);

  if (isAuthenticated === null) {
    return <div className="p-6">Ë™çË®ºÁ¢∫Ë™ç‰∏≠...</div>;
  }

  if (!isAuthenticated) {
    return <Login />; // ‚úÖ Êú™„É≠„Ç∞„Ç§„É≥„Å™„Çâ„É≠„Ç∞„Ç§„É≥„Éö„Éº„Ç∏„ÇíË°®Á§∫
  }

  return (
    <div className="flex min-h-screen">
      {/* Sidebar */}
      <div className="w-64 bg-gray-100 border-r">
        <Sidebar onSelectView={setView} />
      </div>

      {/* Main Content */}
      <div className="flex-1 bg-white p-6 overflow-auto">
        
        {view === "report" && <BudgetReportPage />}
        {view === "user-report" && <UserReportPage />}
        {view === "dashboard" && <UserMonthlyDashboard />}
        {view === "budget" && <BudgetManagement />}
      </div>
    </div>
  );
}
