import React, { useState } from "react";
import Select from "react-select";

interface ExportModalProps {
  isOpen: boolean;
  onClose: () => void;
  onExport: (startDate: string, endDate: string, format: string) => Promise<boolean>; // ‰øÆÊ≠£ÁÆáÊâÄ: `Promise<boolean>` „ÇíËøî„Åô
}

const ExportModal: React.FC<ExportModalProps> = ({ isOpen, onClose, onExport }) => {
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [format, setFormat] = useState("xlsx");
  const [errorMessage, setErrorMessage] = useState(""); // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏Áî®„ÅÆ state

  if (!isOpen) return null;

  const handleExport = async () => {
    setErrorMessage(""); // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„Çí„É™„Çª„ÉÉ„Éà

    const success = await onExport(startDate, endDate, format); // ‰øÆÊ≠£ÁÆáÊâÄ: `onExport` „ÅÆÁµêÊûú„ÇíÂèñÂæó
    if (success) {
      onClose(); // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„ÇØ„Çπ„Éù„Éº„ÉàÊàêÂäüÊôÇ„Å´„É¢„Éº„ÉÄ„É´„ÇíÈñâ„Åò„Çã
    } else {
      setErrorMessage("ÂØæË±°ÊúüÈñì„Å´Ë©≤ÂΩì„Éá„Éº„Çø„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì"); // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„É©„ÉºÊôÇ„Å´„É°„ÉÉ„Çª„Éº„Ç∏„ÇíË°®Á§∫
    }
  };

  return (
    <>
      {/* ‚úÖ „Ç™„Éº„Éê„Éº„É¨„Ç§„ÇíÈÅ©Áî® */}
      <div className="export-modal-overlay" onClick={onClose}></div>

      {/* ‚úÖ „É¢„Éº„ÉÄ„É´Êú¨‰Ωì */}
      <div className="export-modal" onClick={(e) => e.stopPropagation()}>
        <h2>üì• „Éá„Éº„Çø„Çí„Ç®„ÇØ„Çπ„Éù„Éº„Éà</h2>

        <label>ÈñãÂßãÊó•</label>
        <input type="date" value={startDate} onChange={(e) => setStartDate(e.target.value)} />

        <label>ÁµÇ‰∫ÜÊó•</label>
        <input type="date" value={endDate} onChange={(e) => setEndDate(e.target.value)} />

        <label>„Éï„Ç°„Ç§„É´ÂΩ¢Âºè</label>
        <Select
          options={[
            { value: "xlsx", label: "Excel (.xlsx)" },
            { value: "csv", label: "CSV (.csv)" }
          ]}
          value={{ value: format, label: format === "xlsx" ? "Excel (.xlsx)" : "CSV (.csv)" }}
          onChange={(selectedOption) => setFormat(selectedOption?.value || "xlsx")} // ‰øÆÊ≠£ÁÆáÊâÄ: ÈÅ∏Êäû„Åï„Çå„Åü„Éï„Ç©„Éº„Éû„ÉÉ„Éà„ÇíÂèçÊò†
        />

        {/* ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÇíËµ§Â≠ó„ÅßË°®Á§∫ */}
        {errorMessage && <p style={{ color: "red" }}>{errorMessage}</p>}

        <div className="export-modal-buttons">
          <button className="export-button" onClick={handleExport}>üì§ „Ç®„ÇØ„Çπ„Éù„Éº„Éà</button>
          <button className="export-button cancel" onClick={onClose}>„Ç≠„É£„É≥„Çª„É´</button>
        </div>
      </div>
    </>
  );
};

export default ExportModal;
