import { useState, useEffect, useRef } from "react";
import { FaCog } from "react-icons/fa";
import { useRouter } from "next/navigation";
import ExportModal from "./ExportModal";

export type SidebarView = "calendar" | "report" | "user-report" | "dashboard" | "budget";

type SidebarProps = {
  onSelectView: (view: SidebarView) => void;
};

export default function Sidebar({ onSelectView }: SidebarProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isReportOpen, setIsReportOpen] = useState(false);
  const [isExportOpen, setIsExportOpen] = useState(false);
  const [isExportMyDataOpen, setIsExportMyDataOpen] = useState(false);
  const [permissions, setPermissions] = useState({
    canExportAll: false,
    canViewReport: false,
    canViewUserReport: false,
    canViewDashboard: false,
    canManageBudget: false,
  });
  const router = useRouter();

  const reportRef = useRef<HTMLLIElement | null>(null);
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const fetchPermissions = async () => {
      const userId = localStorage.getItem("userId");
      if (!userId) return;

      try {
        const res = await fetch(`/api/getUserPermissions?userId=${userId}`);
        const data = await res.json();
        console.log("‚úÖ Ê®©Èôê„Éá„Éº„Çø:", data);
        setPermissions(data);
      } catch (error) {
        console.error("‚ùå Ê®©ÈôêÂèñÂæó„Ç®„É©„Éº:", error);
      }
    };

    fetchPermissions();
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("isAuthenticated");
    router.push("/login");
  };

  const handleChangePassword = () => {
    router.push("/change-password");
  };

  const handleExport = async (startDate: string, endDate: string, format: string, userId?: string): Promise<boolean> => {
    try {
      const response = await fetch("/api/exportData", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ startDate, endDate, format, userId })
      });

      if (!response.ok) {
        const errorData = await response.json();
        if (errorData.error === "ÂØæË±°ÊúüÈñì„Å´Ë©≤ÂΩì„Éá„Éº„Çø„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì") return false;
        throw new Error("„Ç®„ÇØ„Çπ„Éù„Éº„ÉàÂá¶ÁêÜ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
      }

      const blob = await response.blob();
      const downloadUrl = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = downloadUrl;
      link.setAttribute("download", `export.${format}`);
      document.body.appendChild(link);
      link.click();
      link.remove();

      return true;
    } catch (error) {
      console.error("‚ùå „Ç®„ÇØ„Çπ„Éù„Éº„Éà„Ç®„É©„Éº:", error);
      return false;
    }
  };

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (reportRef.current && !reportRef.current.contains(event.target as Node)) {
        setIsReportOpen(false);
      }
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsMenuOpen(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <div className="sidebar">
      <h1 className="sidebar-title">Âü∑ÂãôÁÆ°ÁêÜ„Ç∑„Çπ„ÉÜ„É†</h1>
      <ul>
      <li className="report-item" onClick={() => onSelectView("calendar")}>
       <span>üóìÔ∏è „Çø„Ç§„É†„Ç∑„Éº„Éà</span>
       <span className="report-arrow"></span>
     </li>
        <li onClick={() => setIsReportOpen(!isReportOpen)} ref={reportRef}>
          <span>üìë„É¨„Éù„Éº„Éà</span>
          <span className="report-arrow">‚ñ∂</span>

          {isReportOpen && (
            <div className="menu-popup_report">
              <button
                className="export-button"
                onClick={() => setIsExportMyDataOpen(true)}
              >
                üì§ ExportÔºàMy DataÔºâ
              </button>

              {isExportMyDataOpen && (
                <ExportModal
                  isOpen={isExportMyDataOpen}
                  onClose={() => setIsExportMyDataOpen(false)}
                  onExport={async (startDate, endDate, format) => {
                    const userId = localStorage.getItem("userId") || "";
                    return await handleExport(startDate, endDate, format, userId);
                  }}
                />
              )}

              {(permissions.canExportAll || permissions.canViewReport || permissions.canViewUserReport || permissions.canViewDashboard) && (
                <div>
                  {permissions.canExportAll && (
                    <button
                      className="export-button"
                      onClick={() => setIsExportOpen(true)}
                    >
                      üì§ ExportÔºàall DataÔºâ
                    </button>
                  )}

                  {permissions.canViewReport && (
                    <button
                      className="export-button"
                      onClick={() => onSelectView("report")}
                    >
                      üìä ‰∫àÂÆü„É¨„Éù„Éº„Éà
                    </button>
                  )}

                  {permissions.canViewUserReport && (
                    <button
                      className="export-button"
                      onClick={() => onSelectView("user-report")}
                    >
                      üë§ „É¶„Éº„Ç∂„ÉºÂà•„É¨„Éù„Éº„Éà
                    </button>
                  )}

                  {permissions.canViewDashboard && (
                    <button
                      className="export-button"
                      onClick={() => onSelectView("dashboard")}
                    >
                      üìà „É¶„Éº„Ç∂„ÉºÊúàÊ¨°„ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„Éâ
                    </button>
                  )}
                </div>
              )}
            </div>
          )}
        </li>
      </ul>

      {isExportMyDataOpen && (
        <ExportModal
          isOpen={isExportMyDataOpen}
          onClose={() => setIsExportMyDataOpen(false)}
          onExport={async (startDate, endDate, format) => {
            const userId = localStorage.getItem("userId") || "";
            return await handleExport(startDate, endDate, format, userId);
          }}
        />
      )}

      {isExportOpen && (
        <ExportModal
          isOpen={isExportOpen}
          onClose={() => setIsExportOpen(false)}
          onExport={(startDate, endDate, format) => handleExport(startDate, endDate, format)}
        />
      )}

      <div
        className="settings"
        onClick={() => setIsMenuOpen(!isMenuOpen)}
        ref={menuRef}
      >
        <FaCog size={20} />
        <span>ÁÆ°ÁêÜ„É°„Éã„É•„Éº</span>
        {isMenuOpen && (
          <div className="menu-popup">
            {permissions.canManageBudget && (
             <button
               className="budget-button"
               onClick={() => onSelectView("budget")} // ‚úÖ „É´„Éº„Çø„ÉºÈÅ∑Áßª„Åß„ÅØ„Å™„Åè„ÄÅ„Éì„É•„ÉºÂàáÊõø
             >
              üí∞ ‰∫àÁÆóÁÆ°ÁêÜ
             </button>
            )}
            <button className="pw-change-button" onClick={handleChangePassword}>
              üîë PWÂ§âÊõ¥
            </button>
            <button className="logout-button" onClick={handleLogout}>
              „É≠„Ç∞„Ç¢„Ç¶„Éà
            </button>
          </div>
        )}
        

      </div>
    </div>
  );
}
